container {
  border: 1px solid rgba(1, 1, 1, .1);
  border-radius: 23px;
  width: 100%;
  height: auto;
  display: block;
  background-image: url('noise.png');
  background-size: 33%;
  background-repeat: repeat;
  padding: 25px;
  box-sizing: border-box;
}

rounded {
  border-radius: 2px;
  min-width: 50%;
  max-width: 500px;
  min-height: 50px;
  margin: 20px auto;
  display: block;
  background-color: rgba(250, 250, 250, .9);
  border: 1px solid gray;
  padding: 30px 18px ;
}
